@import "https://fonts.googleapis.com/css2?family=Gloria+Hallelujah&display=swap";
@keyframes fadeInOpa {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeInFromRight {
  0% {
    opacity: 0;
    right: -40rem;
  }

  100% {
    opacity: 1;
    right: 0;
  }
}

@keyframes fadeOut {
  100% {
    opacity: 0;
  }
}

@keyframes goUp {
  0% {
    transform: translateY(-9rem);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes navbarXg {
  0% {
    clip-path: polygon(0 0, 100% 0%, 100% 8rem, 25% 8rem, 0 8rem);
  }

  50% {
    clip-path: polygon(0 0, 100% 0%, 100% 40%, 25% 70%, 0 40%);
  }

  100% {
    height: auto;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 25% 100%, 0 100%);
  }
}

@keyframes navbarXgClose {
  0% {
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 25% 100%, 0 100%);
  }

  50% {
    clip-path: polygon(0 0, 100% 0%, 100% 40%, 25% 70%, 0 40%);
  }

  100% {
    height: 8rem;
    clip-path: polygon(0 0, 100% 0%, 100% 8rem, 25% 8rem, 0 8rem);
  }
}

@keyframes skinChosen {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes spacingExpanding {
  0% {
    letter-spacing: .7rem;
  }

  50% {
    letter-spacing: 1rem;
  }

  100% {
    letter-spacing: .7rem;
  }
}

