.button {
  text-transform: uppercase;
  background-color: #0000;
  border: none;
  outline: none;
}

.button-blue {
  cursor: pointer;
  background-color: #2fc1db;
  transition: all .2s linear;
}

.button-blue:hover {
  background-color: #25d8f6;
}

.button-yellow {
  cursor: pointer;
  background-color: #c4b998;
  transition: all .2s linear;
}

.button-yellow:hover {
  background-color: #d4ccb2;
}

.arrow-down {
  width: 1.5rem;
  height: 1.5rem;
  fill: #7e7e7e;
  margin-left: .2rem;
  transition: all .2s linear;
}

#toast {
  width: 0;
  height: 0;
  z-index: 10000;
  position: fixed;
  top: 3.2rem;
  right: 3.2rem;
}

@media only screen and (max-width: 40em) {
  #toast {
    top: 2rem;
    right: 2rem;
  }
}

@media only screen and (max-width: 27.5em) {
  #toast {
    transform: scale(.8);
  }
}

.toast {
  min-width: 35rem;
  max-width: 45rem;
  height: 8rem;
  transition: all .24s linear;
  animation: fadeInFromRight .4s, fadeOut 1s linear 4s forwards;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  box-shadow: 0 2px 2px #fff6;
}

.toast-overlay {
  width: 100%;
  height: 100%;
  opacity: .7;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.toast-overlay.welcome {
  background-image: url("the-soul_optimized.0b92dd11.jpeg");
